<style lang="scss">
    .person_info {
        /*下载弹层*/
        .layer_upload {
            position: absolute;
            left: calc(50vw - 400px);
            top: calc(50vh - 125px);
            width: 800px;
            height: 250px;
            background-color: white;
            border: 1px solid lightgrey;
            border-radius: 5px;
            z-index: 300;

            .layer_head {
                width: 100%;
                flex: 0 0 auto;
                font-size: 20px;
                height: 25px;
                line-height: 25px;
                text-align: center;
                padding: 10px 0;
                border-bottom: 1px #dcdcdc solid;

                i {
                    font-size: 30px;
                    padding: 0px 10px;
                    position: absolute;
                    right: 5px;
                }
            }

            .layer_file {
                display: flex;
                justify-content: center;
                width: 100%;
                border-bottom: 1px solid lightgrey;
            }
        }

        /*增删改查弹层*/
        .layer_open {
            position: absolute;
            left: calc(50vw - 600px);
            top: calc(50vh - 300px);
            width: 1200px;
            height: 600px;
            background-color: white;
            border: 1px solid lightgrey;
            border-radius: 5px;
            z-index: 300;
            display: flex;
            flex-direction: column;

            .el-input__inner {
                height: 25px;
                line-height: 25px;
            }

            .cont_item {
                width: 25%;
                padding: 5px 0;
                display: flex;

                .item_title {
                    width: calc(40% - 20px);
                    pading: 0 10px
                }

                .item_cont {
                    width: 60%;

                    .el-input__icon {
                        line-height: 25px;
                    }
                }
            }

            /*弹层标题*/
            .layer_head {
                width: 100%;
                flex: 0 0 auto;
                font-size: 20px;
                height: 25px;
                line-height: 25px;
                text-align: center;
                padding: 10px 0;
                border-bottom: 1px #dcdcdc solid;

                i {
                    font-size: 30px;
                    padding: 0px 10px;
                    position: absolute;
                    right: 5px;
                }
            }

            /*编辑条件区域*/
            .layer_cont {
                padding: 10px 20px;
                display: flex;
                flex-wrap: wrap;
                flex: 1 1 auto;

                .cont_item {
                    width: 25%;
                    padding: 5px 0;
                    display: flex;

                    .item_title {
                        width: calc(40% - 20px);
                        pading: 0 10px
                    }

                    .item_cont {
                        width: 60%;

                        .el-input__icon {
                            line-height: 25px;
                        }
                    }

                }
            }

            /*切换选项卡*/
            .layer_card {
                padding: 0 20px;

                .el-tabs__item:not(.is-disabled):hover {
                    color: #5a9aff;
                }

                .el-tabs__content {
                    padding: 5px 0;
                    height: 165px;
                }

                .el-tabs__item {
                    color: #040418;
                    height: 32px;
                    line-height: 32px;
                }

                .el-tabs--border-card {
                    box-shadow: 0 0px 0px 0 rgba(0, 0, 0, .12), 0 0 0px 0 rgba(0, 0, 0, .04);
                }

                .is-active {
                    color: #5a9aff;
                    background-color: #e1ecfc;
                }
            }

            /*功能按钮区域*/
            .layer_btn {
                padding: 0 20px;
                border-top: 1px solid lightgrey;
                margin: 10px 0 30px 0;

                .item_head {
                    width: 100%;
                    background-color: #f5f7fa;
                    padding: 5px 5px;

                }

                /*人员异动*/
                .person_change, .contact_add, .person_leave, .photo_upload {
                    border: 1px solid lightgrey;
                    display: flex;
                    flex-wrap: wrap;
                }
            }
        }
    }



</style>



